import { colors, textColors } from "./index";
import { castColorHue } from "./types";
var colorMap = colors;
var textColorMap = textColors;
// Constants
var DEFAULT_HUE = "grey";
var DEFAULT_SHADE = "500";
var DEFAULT_COLOR = DEFAULT_HUE + "-" + DEFAULT_SHADE;
export function stdColor(input) {
    if (!input)
        return undefined;
    if (colorMap[input])
        return input;
    var hue = stdHue(input);
    var shade = stdShade(input);
    var colorKey = hue + "-" + shade;
    if (colorMap[colorKey])
        return colorKey;
    return undefined;
}
export function stdHue(input) {
    if (!input.includes("-"))
        return castColorHue(input);
    return castColorHue(input.substring(0, input.indexOf("-")));
}
export function stdShade(input) {
    if (!input.includes("-"))
        return DEFAULT_SHADE;
    return input.substring(input.indexOf("-") + 1, input.length);
}
export function toHex(input) {
    return colorMap[colorOrDefault(input)];
}
export function colorOrDefault(input, defaultColor) {
    var color = stdColor(input);
    if (color)
        return color;
    var defColor = stdColor(defaultColor);
    if (defColor)
        return defColor;
    return DEFAULT_COLOR;
}
export function getHoverColor(input) {
    var color = stdColor(input);
    var hue = stdHue(color || DEFAULT_COLOR);
    var shade = parseInt(stdShade(color || DEFAULT_COLOR));
    if (isNaN(shade))
        return DEFAULT_COLOR;
    var nextShade = shade + 200 > 900 ? shade - 200 : shade + 200;
    return hue + "-" + nextShade;
}
export function getTextColor(input) {
    var color = colorOrDefault(input, "white");
    return textColorMap[color];
}
