export function castIconType(value) {
    switch (value) {
        case "ArrowBack":
        case "ArrowDropDown":
        case "ArrowDropUp":
        case "ArrowLeft":
        case "ArrowRight":
        case "Autorenew":
        case "CalendarToday":
        case "Cancel":
        case "Check":
        case "CheckBox":
        case "CheckBoxOutlineBlank":
        case "CheckCircle":
        case "CheckCircleOutline":
        case "ChevronRight":
        case "CloudUpload":
        case "Delete":
        case "Edit":
        case "RadioButtonChecked":
        case "RadioButtonUnchecked":
        case "Save":
        case "Search":
            return value;
        default:
            return undefined;
    }
}
