export function castColorHue(input) {
    switch (input) {
        case "amber":
        case "blue":
        case "bluegrey":
        case "brown":
        case "cyan":
        case "deeporange":
        case "deeppurple":
        case "green":
        case "grey":
        case "indigo":
        case "lightblue":
        case "lightgreen":
        case "lime":
        case "orange":
        case "pink":
        case "purple":
        case "red":
        case "teal":
        case "yellow":
            return input;
        default:
            return undefined;
    }
}
